.carousel {
    margin: 0px 10px;
}

.carousel-control-prev-icon {
    filter: invert(100);
    margin-top: -350px;
    margin-left: -150px;
    transform: scale(1.5);
}

.carousel-control-next-icon {
    filter: invert(100);
    margin-top: -350px;
    margin-right: -150px;
    transform: scale(1.5);
}

.cards{
    display: grid;
    place-items: center;
    grid-template-columns: auto auto auto auto;
    margin-left: -100px;
}

#card-item{
    margin: 0 10%;
}